// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$black: #000000;
$white: #ffffff;
$pink: #f393C9;
$pinkL: #fccada;
$pinkD: #fb70aa;
$red: red;

// FONTS

$font-main: 'Lato-Regular', 'Arial', sans-serif;
$font-accent: 'Lato-Bold', 'Arial', sans-serif;
$font-light: 'Lato-Light', 'Arial', sans-serif;
$font-mid: 'Lato-Semibold', 'Arial', sans-serif;
$font-italic: 'Lato-Italic', 'Arial', sans-serif;
// breakpoints

$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

// for mobile

$screen-xs-max: 1199px;
