.footer {
  &-top {
    padding: 4px;
    border: 1px solid #f393C9;
    border-radius: 5px;
    &__link {
      text-decoration: none;
      color: $black;
      font-weight: bold;
    }
  }
  &-copy {
    color: $white;
    text-align: center;
    font-size: 9pt;
    padding: 4px;
    border-radius: 7px;
    box-shadow: 0 0 10px rgba($black, 0.5);
  }
}