.account {
  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title {
    text-align: center;
    margin-top: 10px;
  }
  &__btn {
    margin-top: 0;
    padding: 7px;
    border-radius: 5px;
    text-shadow: 0 1px 3px #666;
    cursor: pointer;
    background: #DDDDDD;
    width: max-content;
    height: max-content;
  }
  .cheap-payment__wrap {
    padding: 0;
    max-width: 420px;
    width: auto;
    overflow: hidden;
    max-height: 0;
    transition: max-height 1s;
    &--active {
      max-height: 5000px;
    }
  }
  .cheap-payment__block-wrap {
    margin-top: 0;
    margin-bottom: 10px;
    display: flex;
    width: 100%;
    & > span {
      display: flex;
      width: 31%;
      font-weight: bold;
      justify-content: flex-end;
      margin-right: 20px;
    }
  }
  .cheap-payment__block {
    align-items: center;
    margin-top: 0;
    margin-bottom: 10px;
    display: flex;
    width: 100%;
    & > span {
      display: flex;
      width: 31%;
      font-weight: bold;
      justify-content: flex-end;
      margin-right: 20px;
    }
  }
  .cheap-payment__wrapper {
    display: flex;
    width: 64%;
  }
  .cheap-payment__radio-label {
    flex-direction: row;
    align-items: center;
    span {
      margin-top: 0;
      margin-left: 10px;
      font-weight: normal;
    }
  }
  .cheap-payment__textarea {
    display: flex;
    flex-wrap: wrap;
    & > span {
      margin-right: 20px;
    }
    .cheap-payment__wrapper {
      flex-direction: column;
    }
  }
}
.cheap-payment__select {
  display: flex;
  margin-bottom: 10px;
  flex-wrap: wrap;
  span {
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
    margin-right: 20px;
  }
  select {
    margin: 1px;
    border: #5C9EC4 solid 1px;
    border-radius: 5px;
    color: #686868;
    padding: 5px 10px;
    font: 400 13px Tahoma;
  }
  option {
    font-weight: normal;
    display: block;
    white-space: pre;
    min-height: 1.2em;
    padding: 0px 2px 1px;
  }
}
.account__sms {
  width: 100%;
  background-color: #F4B7D0;
}
.account__no-sms {
  font-size: 16px;
  color: #7d7d7d94;
  cursor: default;
  display: block;
  padding: 20px 0;
  text-align: center;
}
@media (max-width: 402px) {
  .account .cheap-payment__block-wrap {
    justify-content: center;
  }
}