.header {
  &__top {
    display: flex;
    justify-content: flex-end;
    padding-right: 15px;
  }
  &__box {
    display: flex;
    padding: 8px 0 8px 25px;
    text-decoration: none;
    color: #1919197a;
    transition: color .3s;
    &:hover {
      color: #5f5f5f;
    }
  }
  &__svg {
    width: 1em;
    height: 1em;
    margin-right: 10px;
  }
  &__logo {
    box-shadow: 0 0 10px rgba($black, 0.5);
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
      max-width: 300px;
    }
  }
}
.header--account {
  .header__top {
    justify-content: space-between;
    flex-wrap: wrap;
  }
}