*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
  font-size: 16px;
  background: #F4B7D0;
  color: $black;
  font-family: sans-serif;
}

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-y: scroll;
}

body {
  height: 100%;
  width: auto;
  margin: 0;
  padding: 0;
  position: relative;
}
h1, h2, h3, h4, h5, p {
  padding: 0;
  margin: 0;
}
h1, h2 {
  font-size: 16px;
}
a {
  text-decoration: none;
  color: $black;
  transition: color .3s;
}
ul {
  list-style-position: outside;
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: #FFF;
}
li.page-li {
  border-bottom: 2px solid #DF0071;
  padding: 6px;
  position: relative;
}
img {
  border: 0;
  vertical-align: middle;
}

.container {
  max-width: 680px;
  box-shadow: 0 0 13px #000;
  margin-right: auto;
  margin-left: auto;
  padding: 0 1px;
  background-color: #fccada;
}
.btn-disabled {
  pointer-events: none;
  opacity: 0.5;
}
.error {
  border-color: red;
}
#ml_e2f91d {
  margin: 20px auto !important;
}
