.pink {
  background-color: $pink;
}
.pink-l {
  background-color: $pinkL;
}
.pink-d {
  background-color: $pinkD;
}

.tabs {
  &__item {
    display: none;
    &--active {
      display: block;
    }
  }
  &__par {
    span {
      font-weight: bold;
    }
  }
}
.col {
  text-shadow: 0 1px 3px #666;
  padding: 10px;
  font-size: 12pt;
  span {
    border: #A3B2FF solid 1px;
    border-radius: 14px;
    text-decoration: none;
    padding: 1px 5px;
    margin-left: 10px;
    font-weight: bold;
  }
}
.anekdot {
  list-style-position: outside;
  list-style-type: none;
  li {
    border-bottom: 2px solid #DF0071;
    padding: 6px;
    position: relative;
    span {
      font: 13pt sans-serif;
    }
  }
}
.accaunt-people {
  width: 16px;
  height: 16px;
  use {
    fill: #5f5f5f;
  }
}
.svg-rub {
  width: 12px;
  height: 16px;
  margin: 0 5px;
  use {
    fill: #5f5f5f;
  }
}
