.goin {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 25px;
  &__title {
    margin-top: 20px;
    font-size: 16px;
    font-weight: bold;
  }
  &__subtitle {
    margin-bottom: 20px;
  }
  &__capcha {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }
  input {
    min-width: 250px;
    margin: 5px 0;
  }
  .btn {
    min-width: 100px;
    margin: 10px 0 0;
    display: block;
    padding: 7px;
    border: #5C9EC4 solid 1px;
    border-radius: 5px;
    text-shadow: 0 1px 3px #666;
  }
}