.content {
  background-color: #fff;
  iframe {
    margin: 10px auto;
  }
}
.help-top {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  &__rubrika {
    font-weight: bold;
  }
  a {
    padding: 0;
    background-color: transparent;
    border: none;
    font-weight: bold;
    &:hover {
      background-color: transparent;
    }
  }
}
.help-prev {
  a {
    margin-left: 10px;
    padding: 2px 5px 4px;
    display: block;
    width: max-content;
    border-radius: 5px;
    font-size: 14px;
    text-shadow: 0 1px 3px #666;
  }
}
.help {
  &__par {
    padding-left: 10px;
    margin-bottom: 10px;
    span {
      font-weight: bold;
    }
  }
  &__img {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0 auto 10px;
    img {
      box-shadow: 0 0 4px #000;
      max-width: 100%;
      margin: 5px;
    }
  }
  &__img-two {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto 10px;
    width: max-content;
    box-shadow: 0 0 4px #000;
  }
  &__red {
    color: red;
    padding: 5px 5px 15px 10px;
  }
}
#vip {
  .title {
    margin-bottom: 10px;
  }
}
